// src/components/KanbanBoard.js
import React, { useState } from 'react';
import { DndProvider } from 'react-dnd';
import { HTML5Backend } from 'react-dnd-html5-backend';
import Column from './Column';
import FilterPanel from './FilterPanel';

const stages = ['Applied', 'Interviewing', 'Offer', 'Rejected'];

function KanbanBoard({ jobs }) {
  const [jobList, setJobList] = useState(jobs);
  const [filteredJobs, setFilteredJobs] = useState(jobs);

  const handleDrop = (jobId, newStage) => {
    setJobList((prevJobs) =>
      prevJobs.map((job) =>
        job.id === jobId ? { ...job, status: newStage } : job
      )
    );
  };

  const handleFilter = (filters) => {
    const { title, company, status, salaryMin, salaryMax } = filters;
    const filtered = jobList.filter((job) => {
      const matchesTitle = title ? job.title.toLowerCase().includes(title.toLowerCase()) : true;
      const matchesCompany = company ? job.company.toLowerCase().includes(company.toLowerCase()) : true;
      const matchesStatus = status ? job.status === status : true;
      const matchesSalaryMin = salaryMin ? job.salary >= salaryMin : true;
      const matchesSalaryMax = salaryMax ? job.salary <= salaryMax : true;

      return matchesTitle && matchesCompany && matchesStatus && matchesSalaryMin && matchesSalaryMax;
    });
    setFilteredJobs(filtered);
  };

  return (
    <div>
      <FilterPanel onFilter={handleFilter} />
      <DndProvider backend={HTML5Backend}>
        <div className="flex space-x-4 overflow-x-auto p-4">
          {stages.map((stage) => (
            <Column
              key={stage}
              stage={stage}
              jobs={filteredJobs.filter((job) => job.status === stage)}
              onDrop={handleDrop}
            />
          ))}
        </div>
      </DndProvider>
    </div>
  );
}

export default KanbanBoard;
